// --- Helper Function to Load from localStorage ---
const loadFromLocalStorage = <T,>(key: string, defaultValue: T): T => {
    if (typeof window === 'undefined') {
        return defaultValue; // Return default during SSR
    }
    try {
        const savedData = localStorage.getItem(key);
        if (!savedData) return defaultValue;
        const parsed = JSON.parse(savedData);
        // Basic validation if needed (e.g., check if it's an array or object)
        if (typeof defaultValue === 'object' && defaultValue !== null) {
            if (Array.isArray(defaultValue) && !Array.isArray(parsed)) return defaultValue;
            if (!Array.isArray(defaultValue) && typeof parsed !== 'object') return defaultValue;
        }
        return parsed;
    } catch (error) {
        console.error(`Error loading ${key} from localStorage:`, error);
        return defaultValue;
    }
};

// Nueva funcin para cargar templates
const loadScheduleTemplates = (): ScheduleTemplate[] => {
    if (typeof window === 'undefined') return []; // Solo en cliente

    const loadedTemplates: ScheduleTemplate[] = [];
    const templateKeyPrefix = "tpl-"; // O el prefijo/patrn real de tus claves de template

    try {
        for (let i = 0; i < localStorage.length; i++) {
            const key = localStorage.key(i);

            // Busca claves que COINCIDAN con el patrn de template
            if (key && key.startsWith(templateKeyPrefix)) {
                const storedData = localStorage.getItem(key);
                if (storedData) {
                    try {
                        const parsedTemplate = JSON.parse(storedData) as ScheduleTemplate;
                        // Validacin bsica (opcional pero recomendada):
                        if (parsedTemplate && typeof parsedTemplate === 'object' && parsedTemplate.id && parsedTemplate.name) {
                             // Podras querer revivir fechas aqu si es necesario, similar a parseStoredData
                             if (parsedTemplate.createdAt && typeof parsedTemplate.createdAt === 'string') {
                                try {
                                    parsedTemplate.createdAt = parseISO(parsedTemplate.createdAt);
                                } catch (dateError) {
                                     console.error(`Error parsing createdAt date for template ${key}:`, dateError);
                                     // Handle the error, maybe set createdAt to undefined or keep the string
                                }
                             }
                             loadedTemplates.push(parsedTemplate);
                        } else {
                             console.warn(`Dato invlido encontrado para la clave de template: ${key}`);
                        }
                    } catch (parseError) {
                        console.error(`Error parseando JSON para la clave de template ${key}:`, parseError);
                        // Podras eliminar la clave invlida aqu si quieres: localStorage.removeItem(key);
                    }
                }
            }
        }
    } catch (error) {
        console.error("Error cargando templates de localStorage:", error);
        // Podras mostrar un toast de error aqu
    }

    // Opcional: Ordenar los templates por nombre o fecha de creacin si existe
    loadedTemplates.sort((a, b) => {
         const dateA = a.createdAt instanceof Date ? a.createdAt.getTime() : 0;
         const dateB = b.createdAt instanceof Date ? b.createdAt.getTime() : 0;
         if (dateB !== dateA) return dateB - dateA;
         return (a.name || '').localeCompare(b.name || '');
    });


    console.log(`[loadScheduleTemplates] Cargados ${loadedTemplates.length} templates.`);
    return loadedTemplates;
};


