     const handleCopyEmployeeId = (id: string) => {
        navigator.clipboard.writeText(id)
            .then(() => {
                toast({ title: "ID Copiado", description: `ID "${id}" copiado al portapapeles.` });
            })
            .catch(err => {
                console.error("Error al copiar ID:", err);
                toast({ title: "Error al Copiar", variant: "destructive" });
            });
    };


    // --- Drag and Drop Handler ---
    const handleDragEnd = (event: DragEndEvent) => {
        const { active, over } = event;

        console.log("DragEnd Event:", event); // Log the full event

        // Ensure we are dragging an employee and dropping onto a department column for a specific date
        if (active.data.current?.type === 'employee' && over?.data.current?.type === 'department') {
            const employee = active.data.current.employee as Employee;
            const targetDepartmentId = over.data.current.id as string;
            const targetDateStr = over.data.current.date as string; // Date string from droppable data

             if (!employee || !targetDepartmentId || !targetDateStr) {
                 console.error("DragEnd Error: Missing data", { employee, targetDepartmentId, targetDateStr });
                 return;
             }


            const targetDate = parseDateFns(targetDateStr, 'yyyy-MM-dd', new Date());

             // Check if employee is already assigned on this day (optional, depends on rules)
            const daySchedule = getScheduleForDate(targetDate);
            const isAlreadyAssigned = Object.values(daySchedule.assignments).some(deptAssignments =>
                deptAssignments.some(assignment => assignment.employee.id === employee.id)
            );

            if (isAlreadyAssigned) {
                 toast({
                     title: "Colaborador Ya Asignado",
                     description: `${employee.name} ya tiene un turno asignado para el ${format(targetDate, 'PPP', { locale: es })}.`,
                     variant: "destructive",
                     duration: 5000
                 });
                 return;
             }

             // --- Department Compatibility Check ---
             const department = departments.find(d => d.id === targetDepartmentId);
             const employeeBelongsToDepartment = employee.departmentIds?.includes(targetDepartmentId);

             if (department && employee.departmentIds && employee.departmentIds.length > 0 && !employeeBelongsToDepartment) {
                 // Employee has specific departments BUT NOT this one - Show confirmation
                 if (!window.confirm(`Advertencia: ${employee.name} no est asignado directamente a ${department.name}. Deseas continuar con la asignacin?`)) {
                     return; // Cancel if user selects 'Cancel'
                 }
             }


            console.log(`Assigning ${employee.name} (${employee.id}) to dept ${targetDepartmentId} on ${targetDateStr}`);

            // Open shift detail modal after successful drop
            setShiftDetailsTarget({ date: targetDate, departmentId: targetDepartmentId, employee });
            setIsShiftDetailModalOpen(true);
        }
    };


     // --- Shift Management Handlers ---

     // Function to get schedule data for a specific date, creating if it doesn't exist
    const getScheduleForDate = useCallback((date: Date): ScheduleData => {
        const dateKey = format(date, 'yyyy-MM-dd');
        // Ensure scheduleData[dateKey] and its assignments property exist
        const dayData = scheduleData[dateKey] || { date: date, assignments: {} };
        dayData.assignments = dayData.assignments || {}; // Ensure assignments object exists
        return dayData;
    }, [scheduleData]);


    const handleSaveShiftDetails = (details: { startTime: string; endTime: string; includeBreak: boolean; breakStartTime?: string; breakEndTime?: string }) => {
         if (!shiftDetailsTarget || !shiftDetailsTarget.date || !shiftDetailsTarget.departmentId || !shiftDetailsTarget.employee) {
             console.error("Error saving shift: Target data is missing.");
             return;
         }

         const { date, departmentId, employee, assignmentId } = shiftDetailsTarget;
         const dateKey = format(date, 'yyyy-MM-dd');

         setScheduleData(prevSchedule => {
             const newSchedule = { ...prevSchedule };
             const daySchedule = { ...(newSchedule[dateKey] || { date: date, assignments: {} }) };
             daySchedule.assignments = { ...(daySchedule.assignments || {}) }; // Ensure assignments exists
             const deptAssignments = [...(daySchedule.assignments[departmentId] || [])];

             if (assignmentId) { // Editing existing shift
                  const index = deptAssignments.findIndex(a => a.id === assignmentId);
                  if (index !== -1) {
                      deptAssignments[index] = { ...deptAssignments[index], ...details };
                      toast({ title: "Turno Actualizado", description: `Turno de ${employee.name} actualizado.` });
                  } else {
                      console.warn("Shift to edit not found:", assignmentId);
                      toast({ title: "Error", description: "No se encontr el turno a editar.", variant: "destructive" });
                  }
             } else { // Adding new shift
                 const newAssignment: ShiftAssignment = {
                     id: `shift_${uuidv4()}`,
                     employee: employee, // Store the full employee object
                     ...details
                 };
                 deptAssignments.push(newAssignment);
                 toast({ title: "Turno Agregado", description: `Turno agregado para ${employee.name}.` });
             }

             daySchedule.assignments[departmentId] = deptAssignments;
             newSchedule[dateKey] = daySchedule;
             return newSchedule;
         });

         setIsShiftDetailModalOpen(false);
         setShiftDetailsTarget(null); // Reset target
     };


     const handleRemoveShift = (dateKey: string, departmentId: string, assignmentId: string) => {
          console.log(`Attempting to remove shift: dateKey=${dateKey}, deptId=${departmentId}, assignId=${assignmentId}`);
          setScheduleData(prevSchedule => {
              // Shallow copy the schedule up to the necessary level
              const newSchedule = { ...prevSchedule };
              if (!newSchedule[dateKey] || !newSchedule[dateKey].assignments || !newSchedule[dateKey].assignments[departmentId]) {
                  console.warn("Shift removal failed: Path not found in scheduleData", { dateKey, departmentId });
                  return prevSchedule; // Return original state if path doesn't exist
              }

              // Copy the day's schedule and assignments object
              const daySchedule = { ...newSchedule[dateKey] };
              daySchedule.assignments = { ...daySchedule.assignments };

              // Filter out the assignment to be removed
              const originalAssignments = daySchedule.assignments[departmentId] || [];
              const updatedAssignments = originalAssignments.filter(assignment => assignment.id !== assignmentId);

              if (updatedAssignments.length === originalAssignments.length) {
                   console.warn("Shift removal failed: Assignment ID not found", { assignmentId, existingAssignments: originalAssignments });
                   return prevSchedule; // Return original if assignment wasn't found (prevents unnecessary updates)
              }

              // Update the assignments for the department
              daySchedule.assignments[departmentId] = updatedAssignments;

              // If the department now has no assignments, optionally remove the department key
              if (updatedAssignments.length === 0) {
                   // delete daySchedule.assignments[departmentId]; // Optional cleanup
              }

              // Update the schedule for the specific date
              newSchedule[dateKey] = daySchedule;

              console.log("Shift removed successfully. New schedule data:", newSchedule);
              toast({ title: "Turno Eliminado", variant: "destructive" });
              return newSchedule; // Return the updated schedule
          });
      };

